//COLORS
$categoriesBtnColor: #f8f8f8;
$categoriesLink: #403F3F;
//Fonts
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=New+Tegomin&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

// ------------------------------------------NAvbar-----------------------------------------------
.navigation{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 75px 20px 75px;
    .searchSec{
        margin-right: 115px;
        form{
            position: relative;
            .searchInput{
                width: 560px;
                padding: 5px;
                border-radius: 5px;
                border: 1px solid rgb(104, 102, 102);
                &:focus{
                    outline: none;
                    box-shadow: none;
                }
            }
            .searchBtn{
                position: absolute;
                right: 2%;
                top: 10%;
                background-color: transparent !important;
                border: none;
            }
        }
    }
    .icons{
        .bolimlar{
            margin: 12px;
        }
    }
}
// ------------------------------------------CAtegories---------------------------------------------------
.categories{
    padding: 10px 75px 10px 75px;
    display: flex; 
    justify-content: space-between;
    border-top: 1.3px solid #dfdfdf;
    .btn-group{
        display: inline;
        button{
            display: flex;
            align-items: center;
            background-color: $categoriesBtnColor;
            color: #000;
            border: none;
            svg{
                margin-right: 10px;
            }
            &::after{
                margin-left: 35px;
            }
        }
    }
    .links{
        display: flex;
        .link_item{
            list-style-type: none;
            margin: 0 15px;
            
            .link{
                text-decoration: none;
                color: $categoriesLink;
            }
            .active{
                color: #FF5D18;
            }
        }
    }

}
//------------------------------------------Home--------------------------------------------------------
.home{
    background: url(../homebg.jpg) no-repeat;
    height: 32rem;
    background-size: cover;
    .main{
        display: flex;
        .side1{
            margin: 145px auto;
            h3{
                font-size: 50px;
                color: #fff;
                font-family: 'Raleway', sans-serif;
            }
            p{
                color: #fff;
                font-weight: 400;

            }
            .buttons{
                margin-top: 50px;
                a{
                    background-color: #FF4F04;
                    padding: 15px 20px;
                    color: #fff;
                    text-decoration: none;
                    border-radius: 5px;
                    svg{
                        margin-left: 10px;
                    }
                &:nth-child(2){
                    background-color: #5a5a5a54;
                    margin-left: 10px;
                }
                }
            }
        }
        .side2{
            img{
            width: 97%; 
            margin-left: 16px;
            }
        }
    }

}
.recomennded{
    background-color: #f8f8f8;
    padding: 50px 0;
   
    .rp{
        color: #302F2F;
        font-size: 20px;
        text-align: center;
    }
    .rtext{
        opacity:1;
        color:#111111;
        font-family: 'Poppins', sans-serif;
        font-size:25px;
        text-align: center;
    }
    .rproducts{
        .product{
            
            a{
                img{
                    border-radius: 15px;
                    transition: 0.5s ease-in-out;
                    &:hover{
                        transform: scale(1.01 , 1.01);
                    }
                }
            }
            .rtour{
                padding-top: 5px;
                color:#111111;
                font-family: 'Poppins', sans-serif;
                font-size:16px;
                text-align: center !important;

            }
            .rprice{
                background-color:rgb(236, 233, 233) ;
                padding: 5px 15px;
                font-weight: 500;
                color: #FF4F04;
                margin: 10px auto;
                max-width: 60px;
                border-radius: 20px;
            }
        }
    }
    .otherproducts{
        margin-top:  50px;
        display: flex;
        justify-content: center;
        a{
        color: #111111;
        padding: 20px;
        border: 1px solid rgb(128, 128, 128);
        border-radius: 8px;
        transition: 1s ease;
        &:hover{
            text-decoration: none;
            color: #fff;
            background-color: #ee7845;
            border: 1px solid transparent;
            
        }
        }
    }
}
